<template>
    <div class="page-container">
        <el-main>
            <el-row><div class="park-title">{{parkname}}</div></el-row>
            <el-row>
                <div class="plot-list">
                    <template v-for="(item,index) in dataList">
                        <div class="plot-item" :key="index">
                            <div class="flex flex-align-center flex-pack-justify">
                                <div class="flex flex-align-center">
                                    <div class="plot-name m-r-20">{{item.plotname}}</div>
                                    <div class="plot-trace">
                                        <i class="el-icon-position"></i>
                                        <span class="font-14">追溯</span>
                                    </div>
                                </div>
                                <div class="flex flex-align-center">
                                    <div class="plot-info m-r-20" @click.stop="edit(item)">
                                        <i class="el-icon-warning-outline"></i>
                                        <span class="font-14">地块信息</span>
                                    </div>
                                    <el-button size="small" @click.stop="remove(index)">删除</el-button>
                                </div>
                            </div>
                            <div class="plot-statistics flex flex-pack-justify m-t-30">
                                <div class="statistics-item flex-1 flex flex-align-center flex-pack-center">
                                    <i class="el-icon-coin m-r-20"></i>
                                    <div>
                                        <h1>施药</h1>
                                        <h2>{{item.shiyao}}</h2>
                                    </div>
                                </div>
                                <div class="statistics-item flex-1 flex flex-align-center flex-pack-center">
                                    <i class="el-icon-data-analysis m-r-20"></i>
                                    <div>
                                        <h1>施肥</h1>
                                        <h2>{{item.shifei}}</h2>
                                    </div>
                                </div>
                                <div class="statistics-item flex-1 flex flex-align-center flex-pack-center">
                                    <i class="el-icon-place m-r-20"></i>
                                    <div>
                                        <h1>农事</h1>
                                        <h2>{{item.working}}</h2>
                                    </div>
                                </div>
                                <div class="statistics-item flex-1 flex flex-align-center flex-pack-center">
                                    <i class="el-icon-first-aid-kit m-r-20"></i>
                                    <div>
                                        <h1>采集</h1>
                                        <h2>{{item.collect}}</h2>
                                    </div>
                                </div>
                                <div class="statistics-item flex-1 flex flex-align-center flex-pack-center">
                                    <i class="el-icon-truck m-r-20"></i>
                                    <div>
                                        <h1>巡园</h1>
                                        <h2>{{item.touring}}</h2>
                                    </div>
                                </div>
                            </div>
                            <el-row class="m-t-30" type="flex" justify="space-between" align="middle">
                                <el-col :span="12">
                                    <ul class="plot-intro m-r-30 flex-1">
                                        <li class="flex flex-align-center flex-pack-justify">
                                            <span>作物</span>
                                            <p>{{item.crop}}</p>
                                        </li>
                                        <li class="flex flex-align-center flex-pack-justify">
                                            <span>品种</span>
                                            <p>{{item.kind}}</p>
                                        </li>
                                        <li class="flex flex-align-center flex-pack-justify">
                                            <span>预警</span>
                                            <el-badge :value="item.warning" class="item" type="warning"></el-badge>
                                        </li>
                                        <li class="flex flex-align-center flex-pack-justify">
                                            <span>在线设备</span>
                                            <el-badge :value="item.deviceOn" class="item" type="success"></el-badge>
                                        </li>
                                        <li class="flex flex-align-center flex-pack-justify">
                                            <span>离线设备</span>
                                            <el-badge :value="item.deviceOff" class="item" type="info"></el-badge>
                                        </li>
                                    </ul>
                                </el-col>
                                <!-- <el-col :span="6">
                                    <div id="barChart" :style="{width: '100%', height: '250px'}" class="flex-1"></div>
                                </el-col>
                                <el-col :span="6">
                                    <div id="pieChart" :style="{width: '100%', height: '250px'}" class="flex-1"></div>
                                </el-col> -->
                            </el-row>
                        </div>
                    </template>
                    <div class="plot-item" @click="add">
                        <div class="add-item text-center">
                            <i class="el-icon-circle-plus-outline"></i>
                            <p>新建地块</p>
                        </div>
                    </div>
                </div>
            </el-row>
            <el-dialog title="地块信息" :visible.sync="dialogVisible" width="1200px" :close-on-click-modal="false">
                <el-form ref="form" size="small" :rules="rules" :model="form" label-width="150px">
                    <el-tabs tab-position="left">
                        <el-tab-pane label="地块资料">
                            <el-form-item label="地块名称" prop="plotname">
                                <el-input style="width:450px;" v-model="form.plotname" placeholder="请输入地块名称"></el-input>
                            </el-form-item>
                            <el-form-item label="种植面积" prop="plotarea">
                                <el-input style="width:260px;" v-model="form.plotarea" placeholder="请输入面积">
                                    <template slot="append">亩</template>
                                </el-input>
                            </el-form-item>
                            <el-form-item label="联系人姓名" prop="contact">
                                <el-input style="width:260px;" v-model="form.contact" placeholder="请输入联系人姓名"></el-input>
                            </el-form-item>
                            <el-form-item label="联系电话" prop="contacttel">
                                <el-input style="width:260px;" v-model="form.contacttel" placeholder="请输入联系电话"></el-input>
                            </el-form-item>
                            <el-form-item label="土肥情况" prop="plotsoil">
                                <el-table :data="form.plotsoil" size="mini" border style="width: 100%">
                                    <el-table-column prop="name" label="名称">
                                        <template slot-scope="scope">
                                            <el-input size="mini" v-model="scope.row.name"></el-input>
                                        </template>
                                    </el-table-column>
                                    <el-table-column prop="number" label="数量">
                                        <template slot-scope="scope">
                                            <el-input size="mini" v-model="scope.row.number"></el-input>
                                        </template>
                                    </el-table-column>
                                    <el-table-column prop="unit" label="单位">
                                        <template slot-scope="scope">
                                            <el-input size="mini" v-model="scope.row.unit"></el-input>
                                        </template>
                                    </el-table-column>
                                    <el-table-column label="操作" width="80px">
                                        <template slot-scope="scope">
                                            <el-button type="danger" @click="removeRow(scope.$index)" size="mini">删除</el-button>
                                        </template>
                                    </el-table-column>
                                </el-table>
                                <el-button size="mini" type="text" class="m-t-5" @click="addRow">+ 添加成分</el-button>
                            </el-form-item>
                        </el-tab-pane>
                        <el-tab-pane label="物候期">
                            <el-form-item label="日历名称" prop="growth_name">
                                <el-input style="width:450px;" v-model="form.growth_name" placeholder="请输入日历名称"></el-input>
                            </el-form-item>
                            <el-form-item label="种植作物及品种" prop="kind">
                                <el-select style="width:220px;" v-model="form.crop" filterable placeholder="请选择作物" @change="chooseCrop">
                                    <el-option	
                                        v-for="item in cropData"
                                        :key="item.id"
                                        :label="item.name"
                                        :value="item.id">
                                    </el-option>
                                </el-select>
                                <el-select style="width:220px;" v-model="form.kind" allow-create filterable placeholder="请选择品种" @change="chooseKind">
                                    <el-option	
                                        v-for="item in kindData"
                                        :key="item.id"
                                        :label="item.name"
                                        :value="item.id">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="物候期详情" prop="crop_growth">
                                <el-table :data="form.crop_growth" size="mini" border style="width: 100%">
                                    <el-table-column prop="name" label="序号" width="60px">
                                        <template slot-scope="scope">
                                            <span>{{ scope.$index + 1 }}</span>
                                        </template>
                                    </el-table-column>
                                    <el-table-column prop="number" label="物候期名称">
                                        <template slot-scope="scope">
                                            <el-input readonly size="mini" v-model="scope.row.name"></el-input>
                                        </template>
                                    </el-table-column>
                                    <el-table-column prop="unit" label="开始时间">
                                        <template slot-scope="scope">
                                            <el-date-picker v-model="scope.row.startdate" size="mini" type="date" placeholder="选择日期"></el-date-picker>
                                        </template>
                                    </el-table-column>
                                    <el-table-column prop="unit" label="结束时间">
                                        <template slot-scope="scope">
                                            <el-date-picker v-model="scope.row.enddate" size="mini" type="date" placeholder="选择日期"></el-date-picker>
                                        </template>
                                    </el-table-column>
                                    <el-table-column label="操作" width="80px">
                                        <template slot-scope="scope">
                                            <el-button type="danger" @click="clearRow(scope.row)" size="mini">清空</el-button>
                                        </template>
                                    </el-table-column>
                                </el-table>
                            </el-form-item>
                        </el-tab-pane>
                        <el-tab-pane label="地块形状">
                            
                        </el-tab-pane>
                        <el-tab-pane label="地块平面图">
                            <el-form-item label="选择平面图" prop="plottype">
                                <el-radio-group v-model="form.plottype">
                                    <el-radio :label="'1'" border>地块</el-radio>
                                    <el-radio :label="'2'" border>温室</el-radio>
                                    <el-radio :label="'3'" border>大棚</el-radio>
                                    <el-radio :label="'4'" border>自定义</el-radio>
                                </el-radio-group>
                            </el-form-item>
                            <div class="text-center m-t-30">
                                <img v-if="form.plottype == '1'" width="90%" src="@/assets/bg-monitor-default.png" alt="">
                                <img v-if="form.plottype == '2'" width="90%" src="@/assets/bg-monitor-glasshouse.png" alt="">
                                <img v-if="form.plottype == '3'" width="90%" src="@/assets/bg-monitor-greenhouse.png" alt="">
                                <div v-if="form.plottype == '4'" style="min-height: 300px">
                                    <el-upload
                                        action="https://jsonplaceholder.typicode.com/posts/"
                                        :file-list="form.plotsrc">
                                        <el-button size="small" type="primary">上传图片</el-button>
                                        <div slot="tip" class="m-t-10">只能上传jpg/png文件，且不超过500kb</div>
                                    </el-upload>
                                </div>
                            </div>
                        </el-tab-pane>
                    </el-tabs>
                </el-form>
                <span slot="footer" class="dialog-footer">
                    <el-button size="small" @click="dialogVisible = false">取消</el-button>
                    <el-button size="small" type="primary" @click="save">保存</el-button>
                </span>
            </el-dialog>
        </el-main>
    </div>
</template>

<script>
import { search, add, edit, getCropList, getCalendarList } from '@/api/plot'
import qs from 'qs'

/* // 引入基本模板
let echarts = require('echarts/lib/echarts')
// 引入柱状图组件
require('echarts/lib/chart/bar')
require('echarts/lib/chart/pie')
// 引入提示框和title组件
require('echarts/lib/component/tooltip')
require('echarts/lib/component/title') */
export default {
    data() {
        return {
            parkid: 0,
            parkname: '',
            dataList: [],
            isAdd: true,
            dialogVisible: false,
            form: {
                plotname: '',
                crop: '',
                kind: '',
                plotarea: '',
                contact: '',
                contacttel: '',
                plotsoil: [],
                plottype: '1',
                plotsrc: [],
                coordinates: '',
                growth_name: '',
                crop_growth:[],
            },
            cropData: [],
            kindData: [],
            rules: {
                plotname: [
                    { required: true, message: '请输入地块名称', trigger: 'blur' },
                ],
                plotarea: [
                    { required: true, message: '请输入面积', trigger: 'blur' },
                ],
                kind: [
                    { required: true, message: '请输入面积', trigger: 'change' },
                ],
                growth_name: [
                    { required: true, message: '请输入日历名称', trigger: 'blur' }
                ]
            }
        }
    },
    methods: {
        /* drawLine(){
            let barChart = echarts.init(document.getElementById('barChart'))
            barChart.setOption({
                title: { 
                    text: 'barChart',
                    x: 'center',
                    textStyle:{
                        color:'#202425',
                        fontWeight:'normal',
                　　　　 fontSize:16
                    }
                },
                tooltip: {},
                legend: {
                    data:['蒸发量','降水量']
                },
                xAxis: {
                    data: ["2019","2018","2017"]
                },
                yAxis: {
                    show: false
                },
                series: [
                    {
                        name: '蒸发量',
                        type: 'bar',
                        itemStyle:{
                            normal:{
                                color:'#39bc54'
                            }
                        },
                        data: [36, 20, 52]
                    },
                    {
                        name: '降水量',
                        type: 'bar',
                        itemStyle:{
                            normal:{
                                color:'#44BBEE'
                            }
                        },
                        data: [5, 20, 82]
                    },
                ]
            });
            window.addEventListener("resize", () => { barChart.resize();});

            
            let pieChart = echarts.init(document.getElementById('pieChart'))
            pieChart.setOption({
                title: { 
                    text: 'pieCharts',
                    x: 'center',
                    textStyle:{
                        color:'#202425',
                        fontWeight:'normal',
                　　　　 fontSize:16
                    }
                },
                color:['#39bc54', '#44BBEE'],
                tooltip: {
                    trigger: 'item',
                    formatter: "{a} <br/>{b}: {c} ({d}%)"
                },
                series: [
                    {
                        name:'访问来源',
                        type:'pie',
                        radius : ["50%", "70%"],
                        avoidLabelOverlap: false,
                        label: {
                            normal: {
                                show: false,
                                position: 'center'
                            },
                            emphasis: {
                                show: true,
                                textStyle: {
                                    fontSize: '16',
                                    fontWeight: 'bold'
                                }
                            }
                        },
                        labelLine: {
                            normal: {
                                show: false
                            }
                        },
                        data:[
                            {value:335, name:'直接访问'},
                            {value:310, name:'邮件营销'}
                        ]
                    }
                ]
            });
            window.addEventListener("resize", () => { pieChart.resize();});
        }, */
        initData() {
            if (!isNaN(Number(this.$route.params.id))) {
                this.parkid = Number(this.$route.params.id)
                this.doSearch(qs.stringify({parkid: this.parkid}))
            }
        },
        doSearch(data){
            search(data).then(res => {
                if(res.code == 1){
                    this.parkname = res.data.parkname
                    this.dataList = res.data.data
                }else{
                    this.$message.error(res.msg);
                }
            }).catch(err => {
                this.$message.error(err);
            })
        },
        add(){
            this.isAdd = true
            this.dialogVisible = true
            this.resetForm()
            this.ininPlant()
        },
        edit(row){
            this.isAdd = false
            this.dialogVisible = true;
            this.resetForm()
            this.ininPlant()
            this.form = row
        },
        remove(index){
            this.$confirm('此操作将删除该地块, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$message({
                    type: 'success',
                    message: '删除成功!'
                });
            }).catch(() => {
                   
            })
        },
        addRow(){
            let item = {
                name: '',
                number: '',
                unit: ''
            }
            this.form.plotsoil.push(item)
        },
        removeRow(index){
            this.form.plotsoil.splice(index,1)
        },
        save(){
            this.$refs['form'].validate((valid) => {
                if (valid) {
                    let flag = true
                    this.form.crop_growth.forEach(item => {
                        if(item.startdate == ''||item.enddate==''){
                            flag = false
                        }
                    });
                    if(flag){
                        this.form.parkid = this.parkid
                        if(this.isAdd){
                            add(qs.stringify(this.form)).then(res => {
                                if(res.code == 1){
                                    this.$message({
                                        message: res.msg,
                                        type: 'success'
                                    });
                                    this.doSearch(qs.stringify({parkid: this.parkid}))
                                    this.dialogVisible = false
                                }else {
                                    this.$message.error(res.msg);
                                }
                            }).catch(err => {
                                this.$message.error(err);
                            })
                        }else{
                            edit(qs.stringify(this.form)).then(res => {
                                if(res.code == 1){
                                    this.$message({
                                        message: res.msg,
                                        type: 'success'
                                    });
                                    this.doSearch(qs.stringify({parkid: this.parkid}))
                                    this.dialogVisible = false
                                }else{
                                    this.$message.error(res.msg);
                                }
                            }).catch(err => {
                                this.$message.error(err);
                            })
                        }
                    }else{
                        this.$message.error("请检查您的输入！");
                        return false;
                    }
                } else {
                    this.$message.error("请检查您的输入！");
                    return false;
                }
            });
        },
        resetForm() {
            this.form = {
                plotname: '',
                crop: '',
                kind: '',
                plotarea: '',
                contact: '',
                contacttel: '',
                plotsoil: [],
                plottype: "1",
                plotsrc: [],
                coordinates: '',
                growth_name: '',
                crop_growth: []
            }
        },
        ininPlant(){
            getCropList().then(res => {
                if(res.code == 1){
                    for (let i in res.data) {
                        this.cropData.push(res.data[i]);
                    }
                }else {
                    this.$message.error(res.msg);
                }
            }).catch(err => {
                this.$message.error(err);
            })
        },
        chooseCrop(value){
            this.form.kind = '';
            this.kindData = [];
            this.cropData.forEach(item => {
                if( value == item.id){
                    for (let i in item.child) {
                        this.kindData.push(item.child[i]);
                    }
                    return;
                }
            });
        },
        chooseKind(value){
            this.form.crop_growth = [];
            getCalendarList().then(res => {
                if(res.code == 1){
                    res.data.forEach(item => {
                        if( value == item.id){
                            item.data.forEach(it => {
                                let row = {
                                    name: it.growthname,
                                    startdate: '',
                                    enddate: ''
                                }
                                this.form.crop_growth.push(row)
                            });
                        }
                    });
                }else {
                    this.$message.error(res.msg);
                }
            }).catch(err => {
                this.$message.error(err);
            })
            
        },
        clearRow(row){
            row.startdate = ''
            row.enddate = ''
        }
    },
    created() {
        this.initData()
    },
    mounted() {
        //this.drawLine();
    }
}
</script>

<style>

</style>


