import request from '@/utils/request'

export function search(data){
    return request({
        url: 'place/list',
        method: 'post',
        data
    })
}

export function add(data){
    return request({
        url: 'place/add',
        method: 'post',
        data
    })
}

export function edit(data){
    return request({
        url: 'place/edit',
        method: 'post',
        data
    })
}

export function getCropList(){
    return request({
        url: 'place/crop',
        method: 'post'
    })
}

export function getCalendarList(){
    return request({
        url: 'Phenological/list',
        method: 'post'
    })
}


